@import 'theme/theme';
@import 'owl.carousel/dist/assets/owl.carousel.min';
.main {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.index {
    &.landing {
        .btn-block {
            top: 0;
            @include media-breakpoint-down(lg) {
                top: -4.25rem;
            }
        }
        h1 {
            .title-decor {
                position: relative;
                &::after {
                    content: "";
                    display: block;
                    position: absolute;
                    background-image: url("../img/title-decor.svg");
                    background-repeat: no-repeat;
                    width: 10.875rem;
                    height: 10.875rem;
                    right: -5.438rem;
                    bottom: -4.063rem;
                    z-index: 1;
                }
            }
        }
    }
}

h2 {
    &.title-decor {
        position: relative;
        &::after {
            content: "";
            display: block;
            position: absolute;
            background-image: url("../img/plus.png");
            background-repeat: no-repeat;
            width: 2.5rem;
            height: 2.5rem;
            left: -.625rem;
            top: -.5rem;
            z-index: -1;
        }
    }
}

.partners,
.partners-home {
    .owl-carousel .owl-item img {
        height: 1.625rem;
        object-fit: contain;
    }
}

.img-xs {
    height: 13rem;
    object-fit: cover;
}

.img-sm {
    height: 21.25rem;
    object-fit: contain;
    @include media-breakpoint-down(md) {
        height: 11.063rem;
    }
}

.img-md {
    height: 28.125rem;
    object-fit: contain;
    @include media-breakpoint-down(md) {
        height: 14.25rem;
    }
}

.product-img {
    &.img-md {
        height: 46rem;
        object-fit: contain;
        @include media-breakpoint-down(md) {
            height: 28.125rem;
        }
    }
}

.img-lg {
    height: 43rem;
    object-fit: cover;
    @include media-breakpoint-between(sm,
    lg) {
        object-fit: contain;
    }
    @include media-breakpoint-down(sm) {
        object-fit: cover;
        height: 33.313rem;
    }
}

.img-shadow {
    display: block;
    position: relative;
    &::before {
        content: "";
        position: absolute;
        height: 50%;
        width: 100%;
        bottom: 0;
        left: 0;
        right: 0;
        background: linear-gradient(180deg, rgba(28, 28, 28, 0) 0, #000000 100%);
    }
}

.games {
    .owl-carousel .owl-item img {
        width: 91px;
        height: 91px;
    }
}

.main-circle {
    background-color: $yellow;
    width: 2.563rem;
    height: 2.563rem;
}

.icon-style {
    background-color: $white;
    width: 1.5rem;
    height: 1.5rem;
}

.scroll-top {
    background-color: $white;
    width: 3.75rem;
    height: 3.75rem;
    bottom: 6rem;
    right: 1.5rem;
}

.text-copyright {
    font-weight: 300;
    color: $gray-202;
    font-size: .75rem;
}

.card {
    padding: 1.125rem 1.188rem;
    .card-text {
        font-size: .875rem;
    }
    &.bg-card {
        background-color: rgba(18, 18, 19, .9);
    }
}

.recaptcha-style {
    transform: scale(0.6);
    -webkit-transform: scale(0.6);
    transform-origin: 0 0;
    -webkit-transform-origin: 0 0;
    height: 8vh;
    @include media-breakpoint-down(md) {
        transform: scale(0.4);
        -webkit-transform: scale(0.4);
        height: 5vh;
    }
}
